<template>
  <div>
    <div v-if="tabStateNo == 0">
      <PostVue :postData="item" :postNo="i" v-for="(item, i) in vuestaData" :key="i"></PostVue>
    </div>

    <div v-if="tabStateNo == 1">
      <!-- 필터선택페이지 -->
      <div :class="filter" class="upload-image" :style="{backgroundImage:`url(${upFileUrl})`}"></div>
      <div class="filters">
        <FilterBox :upFileUrl="upFileUrl" v-for="(filter, i) in filters" :key="i" :filter="filter">
          <template v-slot:a><span>{{filter}}</span></template>
        </FilterBox>
      </div>
    </div>

    <div v-if="tabStateNo == 2">  
      <!-- 글작성페이지 -->
      <div :class="filter" class="upload-image" :style="{backgroundImage:`url(${upFileUrl})`}"></div>
      <div class="write">
        <textarea class="write-box" placeholder="write!" @input="$emit('upTextInput', $event.target.value)"></textarea>
      </div>
    </div>
  </div>

  <!-- <button @click="updateTabNo(0)">리스트</button>
  <button @click="updateTabNo(1)">업로드</button>
  <button @click="updateTabNo(2)">글쓰기</button> -->
</template>

<script>
import PostVue from './PostVue.vue';
import FilterBox from './FilterBox.vue'

export default {
  name : "ContainerVue",
  components: {
    PostVue,
    FilterBox
  },
  data(){
    return {
      writeText : this.upText,
      filters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                  "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                  "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
    }
  },
  props:{
    vuestaData : Array,
    tabStateNo : Number,
    upFileUrl : String,
    upText : String,
    filter : String,
  },
  methods:{
    updateTabNo(tabNumb){
      // console.log(tabNumb);
      // console.log(this.tabStateNo);
      this.$emit("updateTabState", tabNumb)
    },
    upTextFunc(){
      this.$emit("upTextInput", this.writeText)
    },
  },
  mounted(){
  }

}
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>