<template>
<div class="header">
  <ul class="header-button-left">
    <li>Cancel</li>
  </ul>
  <ul class="header-button-right">
    <li v-if="tabStateNo == 1" @click="tabStateNo++">Next</li>
    <li v-if="tabStateNo == 2" @click="publish">발행</li>
  </ul>
  <img src="./assets/logo.png" class="logo" />
</div>

<h4>{{ $store.state.age }}</h4>
<!-- <button @click="$store.commit('ageIncrement', 10)">버튼</button> -->
<button @click="ageIncrement(5)">버튼</button>
<!-- <button @click="$store.commit('nameChange')">버튼</button> -->

<p>{{$store.state.more}}</p>
<button @click="$store.dispatch('getData')">더보기</button>

<p>{{now2}} {{counter}}</p>
<button @click="counter++">나우버튼</button>
<p>{{name}} {{age}} {{like}} {{myName}}</p>

<ContainerVue v-bind:vuestaData="vuestaData" :tabStateNo="tabStateNo" @updateTabState="tabStateNo=$event" :upFileUrl="upFileUrl" @upTextInput="upText=$event" :filter="filter" />
<button @click="more">more</button>

<div class="footer">
  <ul class="footer-button-plus">
    <input @change="upload" type="file" id="file" class="inputfile" />
    <label for="file" class="input-plus">+</label>
  </ul>
</div>
<p>{{ upText }}</p>
<!-- <div v-if="tabStateNo == 0">내용0</div>
<div v-if="tabStateNo == 1">내용1</div>
<div v-if="tabStateNo == 2">내용2</div>
<button @click="tabStateNo = 0">내용0</button>
<button @click="tabStateNo = 1">내용1</button>
<button @click="tabStateNo = 2">내용2</button> -->


</template>

<script>
import ContainerVue from './components/ContainerVue';
import vuestaData from './assets/vuestaData.js';
import axios from 'axios';
import {mapMutations, mapState} from 'vuex';
// axios.post()

export default {
  name: 'App',
  data() {
    return {
      vuestaData : vuestaData,
      moreNo : 0,
      tabStateNo : 0,
      upFileUrl : "",
      upText : "",
      filter : "",
      counter : 0,
    }
  },
  components: {
    ContainerVue
  },
  computed:{
    now2(){
      return new Date()
    },
    ...mapState(['name','age','like']),
    ...mapState({ myName :'name', })
  },
  methods : {
    ...mapMutations(['setMore', 'likeAction', 'ageIncrement']),
    now(){
      return new Date()
    },
    more(){
      // axios.post('https://codingapple1.github.io/vue/more1.json', {'content':'11111'}).then((result)=>{
      //   console.log(result);
      // }).catch((err)=>{
      //   console.log(err);
      // })
      axios.get(`https://codingapple1.github.io/vue/more${this.moreNo}.json`).then((result)=>{
        var conData = result.data;
        // conData['content'] = "더보기로 요청한 게시물"+this.moreNo+"입니다.";
        this.vuestaData.push(conData);
        this.moreNo++;
      })
    },
    upload(e){
      let upFileData = e.target.files;
      console.log(upFileData[0]);

      this.upFileUrl = URL.createObjectURL(upFileData[0]);
      console.log(this.upFileUrl);

      this.tabStateNo = 1;
    },
    publish(){
      var myData = {
        name: "New Kwon",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.upFileUrl,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.upText,
        filter: this.filter,
      };
      this.vuestaData.unshift(myData);

      this.tabStateNo = 0;
      this.PostImage = "";
      this.upText = "";
    },
    updateTabState(){
    }
  },
  mounted(){
    this.emitter.on('addFilter', (filter)=>{
      this.filter = filter;
    })
  }
}
</script>

