<template>
  <div :class="`${filter} filter-item`" :style="`background-image:url(${upFileUrl})`" @click="fire()">
    <slot name="a"></slot>
  </div> 
</template>

<script>
export default {
    name : 'FilterBox',
    data(){
      return{
        msg : 'ohoh',
      }
    },
    props : {
        upFileUrl : String,
        filter : String
    },
    methods : {
      fire(){
        this.emitter.emit('addFilter',this.filter)
      }
    }

}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>