import { createStore } from 'vuex'
import axios from 'axios'
import vuestaData from './assets/vuestaData'

// Create a new store instance.
const store = createStore({
  state () {
    return {
      count: 0,
      name : "kim vuex store",
      age : 20,
      like : 30,
      likeCount : 0,
      vuestaData : vuestaData,
      more : {}
    }
  },
  mutations: {
    increment (state) {
      state.count++
    },
    nameChange(state){
      state.name = 'park vuex store'
    },
    ageIncrement(state, payload){
      state.age += payload
    },
    likeAction(state, postNo){
      if( state.vuestaData[postNo].liked == false ){
        state.vuestaData[postNo].likes += 1;
        state.vuestaData[postNo].liked = true;
        state.likeCount+=1;
      }else if( state.vuestaData[postNo].liked == true ){
        state.vuestaData[postNo].likes -= 1;
        state.vuestaData[postNo].liked = false;
        state.likeCount-=1;
      }
    },
    setMore(state, data){
      state.more = data
    }
  },
  actions : {//ajax
    getData(context){//context <- store
      axios.get(`https://codingapple1.github.io/vue/more0.json`).then((result)=>{
        console.log(result.data);
        context.commit('setMore', result.data);
      })
    }
  }
})

export default store;